



















import { Vue, Component, Prop, Watch } from 'vue-property-decorator';

@Component
export class Modal extends Vue {
    //////////////////// Prop ////////////////////
    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private modalShow: boolean;

    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private headerLabel: string;

    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => 'lg',
    })
    private size: string;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private static: boolean;

    @Prop({
        type: String, // Boolean, Number, String, Array, Object
        default: () => undefined,
    })
    private dialogClass: string;

    @Prop({
        type: Boolean, // Boolean, Number, String, Array, Object
        default: () => false,
    })
    private noEnforceFocus: boolean;

    private showModal: boolean = false;

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    @Watch('modalShow', { immediate: true, deep: true })
    private modalShowChanged(newVal: boolean, oldVal: boolean): void {
        this.showModal = newVal;
    }

    private closeModal(): void {
        this.$emit('close');
    }
}

export default Modal;
Vue.component('modal', Modal);
