import { ServiceDialog, ServiceToast } from '@/../helpers';

interface IFormItem {
    modalShow: boolean;
}

interface IFormData {
    email: string;
    ip: string;
}

import Modal from '@/../components/modal/modal.vue';
import { FakeData } from '@/helpers/fake-data';

@Component({
    components: { Modal },
})
export default class VuePageClass extends Vue {
    private imageBase64: string = FakeData.IMG;

    private formItem: IFormItem = {
        modalShow: false,
    };

    private formData: IFormData = {
        email: '',
        ip: '',
    };

    //////////////////// Vue Life ////////////////////

    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {}
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}

    //////////////////// Init Data ////////////////////

    //////////////////// Init Select Option  ////////////////////

    //////////////////// Form Show ////////////////////

    private clickShowModal(): void {
        this.formItem.modalShow = true;
    }

    private clickCloseModal(): void {
        this.formItem.modalShow = false;
    }

    //////////////////// Form Set Data ////////////////////

    //////////////////// Modify API (save, delete, test)  ////////////////////
    private dataSave(): void {
        this.clickCloseModal();
        ServiceToast.success('Success !!');
    }

    //////////////////// Resolve text ////////////////////

    //////////////////// Modal ////////////////////
}
